container {
  display: flex;
  flex: 1 1 150px;
  justify-content: center;
}
div.item {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 220px;
  max-width: 310px;
  width: 100%;
  background: #000;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  border-radius: 10%;
}
div.item * {
  box-sizing: border-box;
}
div.item img {
  max-width: 100%;
  vertical-align: top;
  height: 210px;
}
div.item i {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 14px;
  color: white;
  width: 60px;
  height: 60px;
  line-height: 30px;
  transform: translate(-50%, -50%) scale(0);
  transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
div.item a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
}
div.item.blue {
  background-color: #34495e;
}
div.item.red {
  background-color: #e74c3c;
}
div.item.green {
  background-color: #16a085;
}
div.item:hover img,
div.item:active {
  opacity: 0.3;
  filter: grayscale(100);
}
div.item * {
  transition: all 0.35s ease-in-out;
}
div.item:hover i,
div.item:active i {
  transform: translate(-50%, -50%) scale(1);
  transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@media screen and (max-width: 700px) {
    .imgLogo{    
        width: 69px;
        height: 45px;
        }
  body {
    margin: 0;
  }
  h1 {
    font-size: 35px;
  }
  .container {
    flex-direction: column;
    flex: 1 1 100px;
    align-items: center;
  }
}
.wrap {
  margin: 40px auto;
  position: relative;
  width: 200px;
}
.circle {
  height: 200px;
  width: 200px;
  background: #f7f8f9;
  border: 4px solid #4aa0db;
  border-radius: 50%;
  cursor: pointer;
  display: block;
}
.envelope {
  height: 0;
  width: 0;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  border-bottom: 40px solid;
  border-left: 60px solid;
  border-right: 60px solid;
  border-top: 40px solid transparent;
  margin: 60px 40px;
  position: absolute;
}
.envelope-top {
  height: 0;
  width: 0;
  border-left: 60px solid transparent;
  border-right: 60px solid transparent;
  border-top: 40px solid;
  margin: 55px 40px;
  position: absolute;
}
.fl-fl {
  background: #000;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 4px;
  width: 190px;
  position: fixed;
  right: -160px;
  z-index: 1000;
  font: normal normal 10px Arial;
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  -ms-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.fa {
  font-size: 30px;
  color: #fff;
  padding: 10px 0;
  width: 40px;
  margin-left: 8px;
}
.fl-fl:hover {
  right: 0;
}
.fl-fl a {
  color: #fff !important;
  text-decoration: none;
  text-align: center;
  line-height: 43px !important;
  vertical-align: top !important;
}
.float-pn:hover {
  background-color: #3b5998;
}
.float-rs:hover {
  background: #45c055;
}
.float-tw:hover {
  background-color: #1ea1f2;
}
.float-ig:hover {
