container {
  display: flex;
  flex: 1 1 150px;
  justify-content: center;
}
div.item {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 220px;
  max-width: 310px;
  width: 100%;
  background: #000;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  border-radius: 10%;
}
div.item * {
  box-sizing: border-box;
}
div.item img {
  max-width: 100%;
  vertical-align: top;
  height: 210px;
}
div.item i {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 14px;
  color: white;
  width: 60px;
  height: 60px;
  line-height: 30px;
  transform: translate(-50%, -50%) scale(0);
  transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
div.item a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
}
div.item.blue {
  background-color: #34495e;
}
div.item.red {
  background-color: #e74c3c;
}
div.item.green {
